body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  line-height: 1.5;
  color: #121212; }

section {
  margin-top: 6em; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.media {
  max-width: 63.75em;
  margin: 0 auto;
  padding: 0 1.25em; }
  .media__photo-wrapper {
    height: 20.625em;
    margin: 0 0 5.9375em; }
  .media__photo {
    height: 100%;
    width: 100%;
    object-fit: cover; }
  .media__header {
    font-size: 2.625rem;
    font-weight: 700;
    line-height: 1.2;
    margin-top: 0;
    margin-bottom: 0.57em; }
  .media__text {
    margin-top: 0;
    margin-bottom: 0; }
  .media__button {
    margin-top: 2em;
    background-color: #E4C1AE;
    border: none;
    border-radius: 32px;
    padding: 1em 2.5em;
    text-align: center;
    font-weight: 700;
    line-height: 1;
    color: #fff;
    cursor: pointer; }
    .media__button:hover {
      background-color: #C1937B; }

@media (min-width: 768px) {
  .media {
    display: flex;
    justify-content: space-between; }
    .media__photo-wrapper {
      flex-basis: 51.53%;
      margin: 0; }
    .media__content-wrapper {
      flex-basis: 38.78%;
      display: flex;
      flex-direction: column;
      justify-content: center; } }

.modal {
  position: relative;
  display: none; }
  .modal.active {
    display: block; }
  .modal__container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(min(64vw, 57.5em));
    background-color: #fff;
    padding: 3.5em calc(min(5.9375em, 11%));
    z-index: 99; }
  .modal__overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: #121212;
    opacity: 0.5; }
  .modal__cross-mark {
    position: absolute;
    height: 1.06em;
    width: 0.125em;
    border: none;
    padding: 0;
    background-color: #121212;
    transform: rotate(45deg);
    right: 3.25em;
    top: 2.72em;
    cursor: pointer;
    transition: all .5s; }
    .modal__cross-mark:hover {
      transform: rotate(45deg) scale(1.5); }
    .modal__cross-mark:after {
      content: '';
      position: absolute;
      width: 1.06em;
      height: 0.125em;
      background-color: #121212;
      left: -0.468em;
      top: 0.468em; }

.modal-content__header {
  font-size: 2.625rem;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 0.57em; }

.modal-content__text {
  margin-top: 0;
  margin-bottom: 0; }
  .modal-content__text strong {
    font-weight: 600; }

.modal-content__button {
  margin-top: 2em;
  background-color: #E4C1AE;
  border: none;
  border-radius: 32px;
  padding: 1em 2.5em;
  text-align: center;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  cursor: pointer;
  display: none; }
  .modal-content__button.active {
    display: initial; }
  .modal-content__button:hover {
    background-color: #C1937B; }
